import {
  ThemeProvider,
  createMuiTheme,
  makeStyles,
} from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import Container from '@material-ui/core/Container'
import CssBaseline from '@material-ui/core/CssBaseline'
import Paper from '@material-ui/core/Paper'
import FormGroup from '@material-ui/core/FormGroup'
import FormLabel from '@material-ui/core/FormLabel'
import FormControl from '@material-ui/core/FormControl'
import Input from '@material-ui/core/Input'
import InputLabel from '@material-ui/core/InputLabel'
import Typography from '@material-ui/core/Typography'
import CircularProgress from '@material-ui/core/CircularProgress'
import LinearProgress from '@material-ui/core/LinearProgress'

import PropTypes from 'prop-types'

import Box from '@material-ui/core/Box'

import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'

import AppBar from '@material-ui/core/AppBar'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'

import IconButton from '@material-ui/core/IconButton'
import ExitToAppIcon from '@material-ui/icons/ExitToApp'
import VisibilityIcon from '@material-ui/icons/Visibility'
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff'
import EditIcon from '@material-ui/icons/EditOutlined'
import ListIcon from '@material-ui/icons/ListAltOutlined'

import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import Divider from '@material-ui/core/Divider'
import ListItemText from '@material-ui/core/ListItemText'

import SwipeableViews from 'react-swipeable-views'

import theme from './theme.json'

const muiTheme = createMuiTheme(theme)

const useStyles = makeStyles((theme) => ({
  mainPaper: {
    height: 434,
    width: 320,
    marginTop: '240px',
    marginLeft: 'calc(50% - 160px)',
    padding: theme.spacing(3),
  },
  feedPaper: {
    display: 'flex',
    height: 434,
    maxWidth: 420,
    minWidth: 320,
    marginTop: '240px',
    marginLeft: 'calc(50% - 160px)',
    /*  padding: theme.spacing(3), */
    backgroundColor: '#939393',
    /* position: 'relative', */
    overflowY: 'auto',
    overflowX: 'hidden',
    '&::-webkit-scrollbar': {
      width: '0.4em',
    },
    '&::-webkit-scrollbar-track': {
      boxShadow: 'inset 0 0 6px rgba(0,0,0,0.00)',
      webkitBoxShadow: 'inset 0 0 6px rgba(0,0,0,0.00)',
    },
    '&::-webkit-scrollbar-thumb': {
      backgroundColor: '#ffee7155',
      borderRadius: '4px',
    },
  },
  input: {
    marginBottom: theme.spacing(3),
  },
  label: {
    marginBottom: theme.spacing(4),
  },
  numberContainer: {
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
  },
  number: {
    padding: '5px 15px',
    color: 'rgba(255, 238, 113, 1)',
    border: '2px solid rgba(255, 238, 113, 0.5)',
    fontSize: '1.375rem',
    boxSizing: 'border-box',
    fontFamily: 'Roboto, Helvetica, Arial, sans-serif',
    fontWeight: '500',
    lineHeight: '1.75',
    borderRadius: '8px',
    letterSpacing: '0.02857em',
    textTransform: 'uppercase',
    display: 'inline-flex',
  },
  logout: {
    position: 'fixed',
    top: '0em',
    right: '0.3em',
    width: 70,
    height: 70,
  },
}))

const Form = ({ token }) => {
  const classes = useStyles(muiTheme)
  const [id, setId] = useState('')
  const [currentId, setCurrentId] = useState(null)
  const [loading, setLoading] = useState(true)
  const [trigger, refetch] = useState(false)

  useEffect(() => {
    typeof window !== 'undefined' &&
      fetch(`${process.env.REACT_APP_SERVER_URL}/getLastCertificateId`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
        .then((response) => response.json())
        .then(({ lastId }) => {
          setCurrentId(lastId)
          setLoading(false)
        })
  }, [setCurrentId, setLoading, trigger, token])

  const handleSubmit = (certificateId) => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/setLastCertificateId`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ` + token,
      },
      body: JSON.stringify({
        certificateId,
      }),
    }).then(() => {
      setId('')
      refetch(!trigger)
    })
  }
  return (
    <Paper className={classes.mainPaper} elevation={5}>
      <Typography variant='h5' color='textSecondary' align='center'>
        Текущий последний номер сертификата
      </Typography>
      <div className={classes.numberContainer}>
        {!loading ? (
          <div className={classes.number}>{currentId}</div>
        ) : (
          <CircularProgress />
        )}
      </div>
      <FormGroup>
        <FormLabel
          className={classes.label}
          children={
            <Typography variant='h5' color='textSecondary' align='center'>
              Установить последний номер сертификата
            </Typography>
          }
        />
        <FormControl className={classes.input}>
          <InputLabel>Номер</InputLabel>
          <Input
            value={id}
            type='number'
            onChange={(e) => setId(e.target.value)}
          />
        </FormControl>
        <FormControl>
          <Button
            disabled={!id}
            color='primary'
            variant='contained'
            onClick={() => handleSubmit(id)}
          >
            Установить
          </Button>
        </FormControl>
      </FormGroup>
    </Paper>
  )
}

const Auth = ({ refetch, trigger }) => {
  const classes = useStyles(muiTheme)
  const [show, setShow] = useState(false)
  const [password, setPassword] = useState('')
  const [error, setError] = useState(false)

  const handleSubmit = (password) => {
    fetch(`${process.env.REACT_APP_SERVER_URL}/login`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        password,
      }),
    })
      .catch((err) => console.log(err))
      .then((response) => response.json())
      .then(({ token, message }) => {
        message === 'Invalid password.' && setError(true)
        token && window.localStorage.setItem('token', token)
        token && refetch(!trigger)
      })
  }
  return (
    <Dialog maxWidth='xs' fullWidth open={true}>
      <DialogTitle>Введите пароль</DialogTitle>
      <DialogContent>
        <FormGroup>
          <FormControl className={classes.input}>
            <InputLabel error={error}>
              {!error ? 'Пароль' : 'Неверный пароль'}
            </InputLabel>
            <Input
              autoFocus
              value={password}
              type={!show ? 'password' : 'text'}
              onChange={(e) => {
                setPassword(e.target.value)
                setError(false)
              }}
              error={error}
              onKeyDown={({ key }) => {
                key === 'Enter' && handleSubmit(password)
                key === 'Escape' && setPassword('')
              }}
              endAdornment={
                <IconButton size='small' onClick={() => setShow(!show)}>
                  {!show ? (
                    <VisibilityIcon style={{ fontSize: '18px' }} />
                  ) : (
                    <VisibilityOffIcon style={{ fontSize: '18px' }} />
                  )}
                </IconButton>
              }
            />
          </FormControl>
        </FormGroup>
        <DialogActions>
          <FormControl>
            <Button
              disabled={!password}
              color='primary'
              variant='outlined'
              onClick={() => handleSubmit(password)}
            >
              Войти
            </Button>
          </FormControl>
        </DialogActions>
      </DialogContent>
    </Dialog>
  )
}

const LogoutDialog = ({ open, setOpen, handleLogout, ...props }) => (
  <Dialog
    {...props}
    maxWidth='xs'
    fullWidth
    open={open}
    onClose={() => setOpen(false)}
  >
    <DialogTitle>Выйти из приложения?</DialogTitle>
    <DialogContent>
      <DialogActions>
        <FormControl>
          <Button
            color='primary'
            variant='outlined'
            onClick={() => {
              setOpen(false)
            }}
          >
            Отмена
          </Button>
        </FormControl>
        <FormControl>
          <Button
            color='primary'
            variant='contained'
            onClick={() => {
              handleLogout()
            }}
          >
            Выйти
          </Button>
        </FormControl>
      </DialogActions>
    </DialogContent>
  </Dialog>
)

const ResendDialog = ({ token, open, refetch, trigger, setOpen, ...props }) => {
  const [loading, setLoading] = useState(false)
  const handleResend = (id) => {
    setLoading(true)
    fetch(`${process.env.REACT_APP_SERVER_URL}/resend`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ` + token,
      },
      body: JSON.stringify({
        id,
      }),
    })
      .catch((err) => {
        setLoading(false)
        window.alert(err)
      })
      .then((response) => response.json())
      .then(({ status }) => {
        setLoading(false)
        setOpen(null)
        refetch(!trigger)
        window.alert(status)
      })
  }
  return (
    <Dialog
      {...props}
      maxWidth='xs'
      fullWidth
      open={!!open}
      onClose={() => setOpen(null)}
    >
      <DialogTitle>Отправить сертификат повторно?</DialogTitle>
      <DialogContent>
        <DialogActions>
          <FormControl>
            <Button
              disabled={loading}
              color='primary'
              variant='outlined'
              onClick={() => {
                setOpen(null)
              }}
            >
              Отмена
            </Button>
          </FormControl>
          <FormControl>
            <Button
              disabled={loading}
              color='primary'
              variant='contained'
              onClick={() => {
                handleResend(open)
              }}
            >
              Отправить
            </Button>
          </FormControl>
        </DialogActions>
      </DialogContent>
      {loading && <LinearProgress variant='query' />}
    </Dialog>
  )
}

function TabPanel(props) {
  const { children, value, index, ...other } = props

  return (
    <div
      role='tabpanel'
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      <Container style={{ height: '720px', overflow: 'hidden' }} maxWidth='md'>
        <Box p={3}>{children}</Box>
      </Container>
    </div>
  )
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
}

const Feed = ({ token }) => {
  const [feed, setFeed] = useState([])
  const [trigger, refetch] = useState(false)
  const [open, setOpen] = useState(null)
  const classes = useStyles(muiTheme)

  useEffect(() => {
    typeof window !== 'undefined' &&
      fetch(`${process.env.REACT_APP_SERVER_URL}/feed`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
        .then((response) => response.json())
        .then(({ feed }) => {
          setFeed(feed)
        })
  }, [token, setFeed, trigger])

  return (
    <Paper elevation={5} className={classes.feedPaper}>
      <List className={classes.feed}>
        {feed?.map((item) => (
          <React.Fragment>
            <ListItem alignItems='flex-start'>
              <ListItemText
                primary={
                  <div
                    style={{ display: 'flex', justifyContent: 'space-between' }}
                  >
                    <Typography color='primary'>{item.name}</Typography>
                    {item.certificateStatus ? null : (
                      <>
                        {!!item.email && (
                          <Button
                            variant='outlined'
                            style={{
                              color: '#a90c0c',
                              borderColor: '#a90c0c',
                              borderRadius: 'calc(1.7em / 2)',
                              marginLeft: '1em',
                              minWidth: '0em',
                              height: '1.7em',
                            }}
                            onClick={() => {
                              setOpen(item.id)
                            }}
                          >
                            {'❗️'}
                          </Button>
                        )}
                      </>
                    )}
                  </div>
                }
                secondary={
                  <React.Fragment>
                    <div style={{ display: 'flex' }}>
                      <div style={{ display: 'flex' }}>
                        <Typography
                          style={{ marginRight: '0.5em' }}
                          component='div'
                          variant='body2'
                          color='textPrimary'
                        >
                          ID:{' '}
                        </Typography>
                        <Typography
                          component='div'
                          variant='body2'
                          color='secondary'
                          style={{
                            marginRight: '1em',
                          }}
                        >
                          {item.certificateId}
                        </Typography>
                      </div>
                      {item.email && (
                        <div style={{ display: 'flex' }}>
                          <Typography
                            style={{ marginRight: '0.5em' }}
                            component='div'
                            variant='body2'
                            color='textPrimary'
                          >
                            email:{' '}
                          </Typography>
                          <Typography
                            component='div'
                            variant='body2'
                            color='secondary'
                          >
                            {item.email}
                          </Typography>
                        </div>
                      )}
                    </div>
                    {item.date}
                    <br />
                    {item.product || ''}
                  </React.Fragment>
                }
              />
            </ListItem>
            <Divider />
          </React.Fragment>
        ))}
        <ResendDialog
          token={token}
          open={open}
          setOpen={setOpen}
          trigger={trigger}
          refetch={refetch}
        />
      </List>
    </Paper>
  )
}

const App = () => {
  const classes = useStyles(muiTheme)
  const [loading, setLoading] = useState(true)
  const [authorized, authorize] = useState(false)
  const [trigger, refetch] = useState(false)

  const [open, setOpen] = useState(false)

  const [tab, setTab] = useState(0)

  const token = window?.localStorage.getItem('token')

  useEffect(() => {
    typeof window !== 'undefined' &&
      fetch(`${process.env.REACT_APP_SERVER_URL}/auth`, {
        headers: {
          Authorization: `Bearer ` + token,
        },
      })
        .then((response) => response.json())
        .then(({ status }) => {
          status === 'Success' && authorize(true)
          setLoading(false)
        })
        .catch(() => {
          authorize(false)
          setLoading(false)
        })
  }, [token, authorize, trigger])

  const handleTabChange = (_, newValue) => {
    setTab(newValue)
  }

  const handleChangeIndex = (index) => {
    setTab(index)
  }

  const handleLogout = () => {
    window.localStorage.removeItem('token')
    setOpen(false)
    setTab(0)
    authorize(false)
    refetch(!trigger)
  }

  return (
    <ThemeProvider theme={muiTheme}>
      <CssBaseline />
      {authorized && (
        <AppBar
          style={{ height: '70px', backgroundColor: 'rgba(132, 132, 132, 1)' }}
        >
          <Tabs
            centered
            value={tab}
            onChange={handleTabChange}
            indicatorColor='primary'
            textColor='primary'
          >
            <Tab value={0} icon={<EditIcon />} style={{ height: '70px' }} />
            <Tab value={1} icon={<ListIcon />} style={{ height: '70px' }} />
          </Tabs>

          <IconButton
            className={classes.logout}
            color='primary'
            onClick={() => setOpen(true)}
          >
            <ExitToAppIcon fontSize='large' />
          </IconButton>
        </AppBar>
      )}
      {!loading ? (
        <>
          {authorized ? (
            <SwipeableViews index={tab} onChangeIndex={handleChangeIndex}>
              <TabPanel value={tab} index={0} /* dir={muiTheme.direction} */>
                <Form token={token} />
              </TabPanel>
              <TabPanel value={tab} index={1} /* dir={muiTheme.direction} */>
                <Feed token={token} /* tab={tab} */ />
              </TabPanel>
            </SwipeableViews>
          ) : (
            <Auth refetch={refetch} trigger={trigger} />
          )}
        </>
      ) : (
        <CircularProgress
          style={{
            height: '80px',
            marginTop: 'calc(50% - 40px)',
            width: '80px',
            marginLeft: 'calc(50% - 40px)',
          }}
        />
      )}
      <LogoutDialog
        token={token}
        open={open}
        setOpen={setOpen}
        handleLogout={handleLogout}
      />
    </ThemeProvider>
  )
}

export default App
